body, html {
  font: 12px/1.5 \5FAE\8F6F\96C5\9ED1, \5B8B\4F53, "Hiragino Sans GB", STHeiti, "WenQuanYi Micro Hei", "Droid Sans Fallback", SimSun, sans-serif;
  width: 100%;
  height: 100%;
  line-height: 1.5;
  font-size: 14px;
  color: #fff; }
input[type=checkbox],
input[type=radio] {
  display: none; }
i.checkbox {
  font-size: 20px; }
i.checkbox:before,
i.radio:before {
  content: "\E608";
  color: #00fffe; }
input[type=checkbox]:checked + i.checkbox:before,
input[type=radio]:checked + i.radio:before {
  content: "\E607";
  color: #00fffe; }
input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #b8d7ff !important; }
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #b8d7ff !important; }
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #b8d7ff !important; }
input:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #b8d7ff !important; }
::-webkit-scrollbar {
  width: 5px;
  height: 5px; }
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #08213f; }
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #08213f; }
